import React, { useState } from 'react';
import Tooltip from './Tooltip'; 


// 
const gpsImportStyle = {
  border: '1px solid #ddd',
  padding: '20px',
  margin: '20px',
  borderRadius: '5px',
};

/*
const previewStyle = {
  backgroundColor: '#f8f8f8',
  border: '1px solid #eee',
  padding: '10px',
  whiteSpace: 'pre-wrap', // 
  overflowX: 'auto',     // 
  maxHeight: '200px',
};
*/

function GPSImport({ onDataImport, onFileSelected }) {
  const [selectedFile, setSelectedFile] = useState(null);
  const [fileContent, setFileContent] = useState('');

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    setSelectedFile(file);

    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        //setFileContent(e.target.result);
        // 
        if (onDataImport) {
          onDataImport(e.target.result, file.name); //   
        }
      };
      reader.readAsText(file); // GPXCSV
    } else {
      //setFileContent('');
      if (onDataImport) {
        onDataImport(null, null); //  null 
      }
    }
    if (onFileSelected) { //  onFileSelected  
      onFileSelected();
    }
  };

  return (
    <div style={gpsImportStyle}>
      <h2>
        GPS
        <Tooltip text={"GPX\nGPX"} />
      </h2>
      <input type="file" accept=".gpx,.tcx,.csv,.txt" onChange={handleFileChange} />
      {/*selectedFile && (
        <div>
          <h3>:</h3>
          <p>{selectedFile.name}</p>
        </div>
      )}
      {fileContent && (
        <div>
          <h3>:</h3>
          <pre style={previewStyle}>{fileContent.substring(0, 200)}...</pre>
          {fileContent.length > 200 && <p>(200)</p>}
        </div>
      )}*/}
    </div>
  );
}

export default GPSImport;