import React, { useState, useEffect } from 'react';
import './PaceInput.css'; // CSS
import Tooltip from './Tooltip'; 

const paceInputStyle = {
    border: '1px solid #ddd',
    padding: '20px',
    margin: '20px',
    borderRadius: '5px',
};

function PaceInput({
    startTime,
    finishTime, //  : FINISH 
    onStartTimeChange,
    onFinishTimeChange, //  : FINISH 
    onSimulate,
    itraIndex,
    onItraIndexChange,
    onFlatPaceInputChange,
    flatPaceInput,
    calculatePaceFromFinishTime,
    calculatedAveragePace
}) {
    const [isButtonFlashing, setIsButtonFlashing] = useState(false);

    useEffect(() => {
        // 
        const timeFormatRegex = /^\d{2}:\d{2}$/; // HH:MM
        if (timeFormatRegex.test(flatPaceInput) && !calculatedAveragePace) {
            setIsButtonFlashing(true);
        } else {
            setIsButtonFlashing(false);
        }
    }, [flatPaceInput, calculatedAveragePace]);

    const handlePaceButtonClick = () => {
        calculatePaceFromFinishTime();
        setIsButtonFlashing(false); // 
    };

    return (
        <div style={paceInputStyle}>
            <h2></h2>
            <div className="input-group time-inputs-container">
                <div>
                    <label htmlFor="startTime">
                         (HH:MM)
                        <Tooltip text={" HH:MM \n:17:0011:00"} />
                    </label>
                    <input
                        type="time"
                        id="startTime"
                        value={startTime}
                        onChange={onStartTimeChange}
                        placeholder=": 09:00"
                    />
                </div>
                <div>
                    <label htmlFor="finishTime"></label>
                    <input
                        type="time"
                        id="finishTime"
                        value={finishTime}
                        onChange={onFinishTimeChange}
                        placeholder=": 09:00"
                    />
                </div>
            </div>
            {/*  ITRA   */}
            <div className="input-group">
                <label htmlFor="itraIndex">
                    ITRA (11000)
                    <Tooltip text={"ITRA400\n\n1300301550551725\n7268258251000"} />
                </label>
                <input
                    type="number"
                    id="itraIndex"
                    value={itraIndex}
                    onChange={onItraIndexChange}
                    min="0"
                    max="1000"
                />
            </div>
            <div className="input-group">
                <label htmlFor="flatPaceInput">
                     (HH:MM)
                    <Tooltip text={"\n\n"} />
                </label>
                <input
                    type="time"
                    id="flatPaceInput"
                    value={flatPaceInput}
                    onChange={onFlatPaceInputChange}
                    placeholder=": 03:30"
                />
                <button
                    type="button"
                    onClick={handlePaceButtonClick}
                    className={isButtonFlashing ? 'flashing-button' : ''}
                    disabled={!flatPaceInput}
                >
                    
                </button>
                {calculatedAveragePace && <p>: {calculatedAveragePace} / km</p>} {/*    */}
            </div>
            <button onClick={onSimulate} disabled={!calculatedAveragePace}></button>
            {!calculatedAveragePace && flatPaceInput && (
                <p style={{ color: 'red' }}></p>
            )}
        </div>
    );
}

export default PaceInput;