import React from 'react';

function PointInfo(props) {
  const { point } = props;

  return (
    <div style={pointInfoStyle}>
      <h3>{point.name}</h3>
      <p>: {point.point_type}</p>
      <p>: {point.cumulative_distance} km</p>
      <p>: {point.section_distance} km</p>
      <p>: {point.elevation} m</p>
      <p>: {(point.section_gradient * 100).toFixed(2)} %</p>
      <p>: {point.surface_type}</p>
      {point.arrival_time && <p>: {point.arrival_time}</p>}
      {point.rest_time !== undefined && <p>: {point.rest_time} </p>}
    </div>
  );
}

const pointInfoStyle = {
  border: '1px solid #ccc',
  padding: '10px',
  margin: '10px',
  borderRadius: '5px',
};

export default PointInfo;
